<template>
    <div class="ycheckbox">
            <van-checkbox v-model="checked1"> 
                <template #icon="props">
                        <img  class="img-icon"  v-if = "disabled" :src="disabledIcon" alt="">
                        <img v-else class="img-icon" :src="props.checked ? activeIcon : inactiveIcon" />
                         <!-- 不可选的时候使用这个照片 -->
                </template> 
            </van-checkbox>
    </div>
</template>
<script>
export default {
    data(){
       return{
            activeIcon:"/images/checkbox1.png",
            inactiveIcon:"/images/checkbox2.png",
            disabledIcon:"/images/checkbox3.png",
            
       }
    },
     props:{
         disabled:{
             default:false
         },
         value:{
             default:true
         }
     },
     computed:{
         checked1:{
              get(){
                  return this.value;
              },
              set(val){ 
                  this.$emit('input', val);
                  this.$emit('change', val);
              }
         }
     }

}
</script>
<style lang="scss" scoped>
.van-checkbox{
   width:(37vw/7.5)!important;
     height:(60vw/7.5)!important;
.van-checkbox__icon{
     width:(37vw/7.5)!important;
     height:(60vw/7.5)!important;
    .img-icon{
        width:(37vw/7.5);
        height:(37vw/7.5);
        transform: translateY(-1vw);
    }
}

}
</style>

