<template>
  <div class="susBall">
    <div
      :class="{'removable':true}"
      @touchstart="handleTouchstart"
      @touchmove.prevent="handleTouchmove"
      @touchend="handleTouchend" 
      :style="'left:'+source.clientX+'px; top:'+source.clientY+'px;'"
    >
      <div @click="toTop" class="reItem animate__animated animate__fadeInUp animate__faster" v-if="show">
          <img src="/images/susball10.png" alt=""> 
      </div>
      <div @click="showPrice = !showPrice" class="reItem animate__animated animate__fadeInUp animate__faster" v-if="show">
          <img src="/images/susball4.png"  v-if="showPrice" alt=""> 
          <img src="/images/susball9.png" v-if="!showPrice" alt=""> 
      </div>
      <div @click="alertPrice" class="reItem animate__animated animate__fadeInUp animate__faster" v-if="show">
          <img src="/images/susball11.png" alt=""> 
      </div>
      <div @click="generateImg" class="reItem animate__animated animate__fadeInUp animate__faster" v-if="show">
          <img src="/images/susball2.png" alt=""> 
      </div>
      <div class="reItem animate__animated animate__fadeInUp animate__faster" @click="show = !show">
           <img v-if="show" src="/images/susball8.png" alt="">   
           <img v-else src="/images/susball16.png" alt="">    
      </div>
    </div>
  </div> 
</template>
<script>
export default {
  data() {
    return {
      //小球初始位置
      source: {
        clientX: 0,
        clientY:200
      },
      relativex: 0,
      relativey: 0,
      show: true,//列表是不是展开
      showPrice:true//是不是需要显示价格
    };
  },
  mounted() {
    //从本地获取到小球的位置信息，
    var susBall =  localStorage.getItem("susBallLocation");
    susBall = JSON.parse(susBall);
    if(susBall){
       this.source = susBall;
    }else{
         //初始化位置信息
         let bWidth = document.body.clientWidth;
         this.source.clientX = bWidth - bWidth / 10 - 35;
    }
  },
  methods: { 
    toTop(){
        window.location.href = "#scrollHeader";
    },
    handleTouchstart(e) {
      let element = e.targetTouches[0];
      //手指的初始位置
      let fx = element.clientX; 
      let fy = element.clientY;

      // 小球的初始位置  
      let ballx = this.source.clientX;
      let bally = this.source.clientY;

      //计算出相对距离
      this.relativex = fx - ballx;
      this.relativey = fy - bally;
    },
    //处理拖拽
    handleTouchmove(e) {
      //  e.stopPropagation()
      //  e.preventDefault();
      // 拖拽中
      let element = e.targetTouches[0];
      // 记录手指的位置
      // 计算出小球半径是多少px
      // 10vw 的话
      // widthPX / 10
      // let bWidth = document.body.clientWidth;
      // bWidth = bWidth / 10 / 2;
      this.source.clientX = element.clientX - this.relativex;
      this.source.clientY = element.clientY - this.relativey;
    },

    handleTouchend(e) {
      // 获取y 的值
      let clientX = e.changedTouches[0].clientX;
      console.log(clientX);
      // 获取屏幕总宽度
      let bWidth = document.body.clientWidth;
      if (clientX > bWidth / 2) {
        console.log("右边", e);
        this.source.clientX = bWidth - bWidth / 10 - 35;
      } else {
        console.log("左边");
        this.source.clientX = 10; 
      }


      //把小球的位置信息保存到本地，然后下次进来的时候自动获取
      localStorage.setItem("susBallLocation",JSON.stringify(this.source));

    },
    generateImg(){
        console.log("生成图片-------");
        let coms =  this.$parent.$refs.appRef;
        coms.generateImg = !coms.generateImg; 
    },
    alertPrice(){
         let coms =  this.$parent.$refs.appRef;
         coms.priceStatus = 'alert';
        
        //  coms.alertPrice();
        // console.log(coms);
    }
  },
  watch:{
      showPrice(e){
        console.log(e)
          if(e == true){
               //说明是显示价格
               let coms =  this.$parent.$refs.appRef;
               coms.priceStatus = 'price';
          }else {
               //说明是隐藏价格
               let coms =  this.$parent.$refs.appRef;
               coms.priceStatus = 'hidden';
          }
      }
  }

};
</script>>
<style lang="scss" scoped>
#app {
  //  background-color: red;
}

.removable {
   width:17vw;
  // height:90vw;
  height: 17vw;
  border-radius: 5vw;
  position: fixed;

  // top: 0px;
  z-index: 999999999;
  // background-color:red;
  // border: 1px solid red;
  display: flex;
  justify-content: flex-end;
  flex-direction: column;

  .reItem { 
    width: 17vw;
    height: 17vw;
    border-radius: 50%;
    // background-color: darkgoldenrod;
    // border: 1px solid darkcyan;

    margin-top:-1vw;
    flex-shrink: 0;

    img{
      width:100%;
      height:100%;
    }
  }
}
</style>
